.services {
  @include page-grid;
  row-gap: 24px;
  margin-bottom: 120px;

  @include on-tablet {
    margin-bottom: 148px;
  }

  @include on-small-desktop {
    margin-bottom: 151px;
  }

  @include on-desktop {
    margin-bottom: 148px;
  }

  &__title-block {
    grid-column: 1 / -1;
    margin-top: 120px;
    margin-bottom: 48px;

    @include on-tablet {
      grid-column: span 3;
      margin-top: 148px;
      margin-bottom: 56px;
    }

    @include on-small-desktop {
      grid-column: span 4;
      margin-top: 251px;
    }
  }

  &__title-text {
    font-size: 14px;
    line-height: 21px;
  }

  &__block {
    display: flex;
    flex-direction: column;
    gap: 24px;

    @include on-tablet {
      gap: 30px;
    }

    @include on-small-desktop {
      gap: 20px;
      margin-top: 287px;
    }

    @include on-desktop {
      gap: 30px;
      margin-top: 280px;
    }

    &--1-3 {
      grid-column: 1 / -1;

      @include on-tablet {
        grid-column: 1 / 4;
      }

      @include on-small-desktop {
        grid-column: 7 / 10;
      }
    }

    &--2-4 {
      grid-column: 1 / -1;

      @include on-tablet {
        grid-column: 4 / -1;
        transform: translateY(-132px);
      }

      @include on-small-desktop {
        grid-column: 10 / -1;
        transform: translateY(-139px);
      }

      @include on-desktop {
        transform: translateY(-132px);
      }
    }
  }

  &__service {
    position: relative;
    background-color: $header-footer-hero-color;
    border-radius: 16px;
    box-shadow: 0 9px 18px 0 #25293108;
    height: 264px;

    @include hover(transform, scale(1.05));
  }

  &__number {
    color: $button-color;
    &-bg {
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: rgba(32, 96, 246, 0.1);
      width: 46px;
      height: 24px;
      border-radius: 23px;
      margin: 32px 0 24px 32px;
    }
  }
  &__desc {
    margin-block: 0;
    margin-left: 32px;
    font-size: 13px;
    font-weight: 600;
    line-height: 19.5px;
  }

  &__button-block {
    display: flex;
    gap: 16px;
    align-items: center;
    margin: 32px;
    height: 40px;

    @include hover (transform, scale(1.2));
  }

  &__button {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-image: url('../images/services/service-button.svg');
    background-repeat: no-repeat;
    background-position: center;

    &-desc {
      font-family: Poppins, sans-serif;
      color: #334563;
      font-size: 15px;
      font-weight: 600;
      line-height: 22.5px;
      text-decoration: none;
    }
  }
}
