  background-color: #2060f6;
  border: none;
  font-family: Poppins;
  font-size: 15px;
  font-weight: 600;
  line-height: 22.5px;
}

.header {
  color: #fff;
  background: #2c2c2c;
  padding-top: 20px;
}

@media (width >= 1024px) {
  .header {
    padding-top: 48px;
  }
}

.header__content {
  display: flex;
  position: relative;
}

.header__logo {
  width: 100%;
  height: 28px;
  transition: transform .3s;
}

.header__logo:hover {
  transform: scale(1.1);
}

.header__nav {
  min-height: 20px;
  margin: 0 auto;
}

.header__list {
  gap: 48px;
}

.header__item:not(:nth-child(n+5)) {
  display: none;
}

@media (width >= 1024px) {
  .header__item:not(:nth-child(n+5)) {
    display: inline-block;
  }
}

.dropdown {
  position: relative;
}

.dropdown__icon {
  cursor: pointer;
  background-color: #2c2c2c;
  background-image: url("menu-icon.ff4e3478.svg");
  border: none;
  width: 24px;
  height: 24px;
  margin-left: 32px;
  transition: transform .3s;
}

.dropdown__icon:hover {
  transform: scale(1.1);
}

@media (width >= 1024px) {
  .dropdown__icon {
    display: none;
  }
}

.dropdown__content {
  z-index: 1;
  box-shadow: 0 2px 5px 0 rgba 0, 0, 0, .1;
  background-color: #fff;
  border-radius: 10px;
  width: 200px;
  height: 0;
  margin-top: 10px;
  position: absolute;
  overflow: hidden;
}

.dropdown--active .dropdown__content {
  height: auto;
}

.dropdown__list {
  margin: 0 auto;
}

.dropdown__item {
  padding: 0;
  list-style: none;
}

.dropdown__link {
  color: #2c2c2c;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-size: 13px;
  font-weight: 700;
  line-height: 20px;
  text-decoration: none;
  transition: opacity .3s;
  display: inline-block;
}

.dropdown__link:hover {
  opacity: .5;
}

.nav {
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  padding: 0;
  list-style: none;
}

.nav__link {
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-size: 13px;
  font-weight: 700;
  line-height: 20px;
  text-decoration: none;
  transition: color .3s;
  display: inline-block;
}

.nav__link:hover {
  color: #aaa;
}

.nav__link--hire-us {
  padding: 0;
  position: relative;
}

.nav__link--hire-us:after {
  content: "";
  pointer-events: none;
  background-color: #2060f6;
  border-radius: 4px;
  height: 2px;
  display: block;
  position: absolute;
  bottom: -8px;
  left: 0;
  right: 0;
}

.nav__link--hire-us-container {
  position: absolute;
  right: 0;
}

