.testimonials {
  &__block {
    @include page-grid;
    row-gap: 24px;

    @include on-tablet {
      row-gap: 30px;
    }
  }

  &__testimonial {
    background-color: $header-footer-hero-color;
    border-radius: 16px;
    box-shadow: 0px 9px 18px 0px #25293108;
    grid-column: 1 / -1;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include hover (transform, scale(1.1));

    @include on-small-desktop {
      grid-column: span 4;
    }
  }

  &__img {
    width: 196px;
    height: 196px;
    margin-block: 32px 24px;
    @include on-tablet {
      width: 250px;
      height: 250px;
    }

    @include on-small-desktop {
      width: 196px;
      height: 196px;
    }

    @include on-desktop {
      width: 250px;
      height: 250px;
    }
  }

  &__quotes {
    width: 24px;
    height: 24px;
  }

  &__text {
    font-size: 13px;
    font-weight: 600;
    line-height: 19.5px;

    &-container {
      height: 80px;
      margin-bottom: 16px;
      margin-inline: 42px;

      @include on-tablet {
        margin-inline: 98px;
        height: 40px;
      }

      @include on-small-desktop {
        margin-inline: 48px;
        height: 80px;
      }

      @include on-desktop {
        margin-inline: 56px;
      }
    }
  }

  &__autor {
    margin: 0;
  }

  &__company {
    margin-block: 4px 32px;
  }
}
