.footer {
  background: $header-footer-hero-bg-color;
  color: $header-footer-hero-color;
  border-radius: 30px 30px 0 0;
  padding-block: 72px 80px;

  @include on-tablet {
    padding-top: 180px;
  }

  &__content {
    @include page-grid;
    row-gap: 80px;

    @include on-tablet {
      row-gap: 120px;
    }
  }

  &__message {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 1 / 5;
    }

    @include on-small-desktop {
      grid-column: 1 / 6;
    }
  }

  &__title {
    color: $header-footer-hero-color;
    font-size: 32px;
    font-weight: 600;
    line-height: 41.6px;
    margin: 0;
    margin-bottom: 48px;

    @include on-tablet {
      margin-bottom: 56px;
    }
  }

  &__input {
    width: 100%;
    height: 41px;
    background-color: $header-footer-hero-bg-color;
    color: $header-footer-hero-color;
    font-family: Poppins;
    font-size: 15px;
    font-weight: 600;
    line-height: 22.5px;
    border: none;
    border-bottom: 1px solid #ffffff;

    @include hover(border-bottom, 1px solid #144ed4);
    @include action(border-bottom, 1px solid #144ed4);

    &:not(:nth-child(n + 3)) {
      margin-bottom: 40px;

      @include on-tablet {
        margin-bottom: 48px;
      }
    }
  }

  &__button {
    grid-column: 1 / -1;
    margin-top: 56px;
    height: 56px;
    width: 100%;
    border-radius: 8px;
    font-family: Poppins;
    font-size: 15px;
    font-weight: 600;
    line-height: 22.5px;

    @include hover (transform, scale(1.1));

    @include on-tablet {
      width: 264px;
      margin-top: 80px;
    }
  }

  &__contact-us {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 1 / 5;
    }

    @include on-small-desktop {
      grid-column: 7 / -2;
    }

    @include on-desktop {
      grid-column: 8 / -2;
    }
  }

  &__name {
    text-transform: uppercase;
    opacity: 50%;
    font-size: 13px;
    font-weight: 700;
    line-height: 19.5px;
    letter-spacing: 3px;
    color: $header-footer-hero-color;
    margin: 0;

    &:nth-child(-n + 4) {
      margin-bottom: 8;
    }

    &:not(:nth-child(-n + 4)){
      margin-bottom: 16px;
    }

    &:not(:nth-child(1)){
      margin-top: 48px;
    }
  }

  &__link {
    color: $header-footer-hero-color;
    text-decoration: none;

    @include hover (color, #aaa);
  }

  &__s-m-links {
    display: flex;
    gap: 32px;
  }

  &__s-m-link {
    height: 32px;
    width: 32px;

    @include hover (transform, scale(1.1));
  }

  &__links {
    grid-column: 1 / -1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 48px;

    @include on-tablet {
      gap: 24px;
    }

    @include on-small-desktop {
      flex-direction: row;
      justify-content: space-between;
    }
  }

  &__list {
    flex-direction: column;
    gap: 24px;

    @include on-tablet {
      flex-direction: row;
      gap: 40px;
    }

    @include on-small-desktop {
      gap: 64px;
    }
  }
}
